"use client";

import React, { useState } from "react";
import PaginaClientes from "@/componentes/PaginaCliente";
import PaginaEmpleados from "@/componentes/PaginaEmpleado";
import PaginaServicios from "@/componentes/PaginaServicios";
import PaginaVentas from "@/componentes/PaginaVentas";
import ModalCliente from "@/componentes/ModalCliente";
import ModalEmpleado from "@/componentes/ModalEmpleado";
import ModalServicio from "@/componentes/ModalServicio";

import { useCliente } from "@/hooks/useCliente";
import { useEmpleado } from "@/hooks/useEmpleado";
import { useServicios } from "@/hooks/useServicios";

import { Cliente } from "@/modelo/cliente";
import { Empleado } from "@/modelo/empleado";
import { Servicio } from "@/modelo/servicio";

export default function TiendaServicioDashboard() {
  const [pagina, setPagina] = useState<"clientes" | "empleados" | "servicios" | "ventas">("clientes");

  // Hooks de cada entidad
  const clienteHook = useCliente();
  const empleadoHook = useEmpleado();
  const servicioHook = useServicios();

  // Guardar cliente
  const guardarCliente = async (cliente: Cliente | Omit<Cliente, "id">) => {
    await clienteHook.registrarCliente(cliente);
    clienteHook.setMostrarModalCliente(false);
  };

  // Guardar empleado
  const guardarEmpleado = async (empleado: Empleado | Omit<Empleado, "id">) => {
    await empleadoHook.guardarEmpleado(empleado);
    empleadoHook.setMostrarModalEmpleado(false);
  };

  // Guardar servicio
  const guardarServicio = async (servicio: Servicio | Omit<Servicio, "id">) => {
    if ("id" in servicio) {
      await servicioHook.actualizarServicio(servicio);
    } else {
      await servicioHook.agregarServicio(servicio);
    }
    servicioHook.setMostrarServicioModal(false);
  };

  return (
    <div className="min-h-screen bg-gray-100 text-gray-800">
      {/* NavBar */}
      <nav className="bg-gray-800 p-4">
        <div className="flex justify-around">
          {["clientes", "empleados", "servicios", "ventas"].map((p) => (
            <button
              key={p}
              onClick={() => setPagina(p as any)}
              className={`px-4 py-2 rounded-md text-white font-semibold ${
                pagina === p ? "bg-blue-600" : "hover:bg-gray-600"
              }`}
            >
              {p.charAt(0).toUpperCase() + p.slice(1)}
            </button>
          ))}
        </div>
      </nav>

      {/* Contenido según página actual */}
      <main className="p-6">
        {pagina === "clientes" && (
          <PaginaClientes
            clientes={clienteHook.clientes}
            setMostrarModalCliente={clienteHook.setMostrarModalCliente}
            setClienteSeleccionado={clienteHook.setClienteSeleccionado}
          />
        )}
        {pagina === "empleados" && (
          <PaginaEmpleados
            empleados={empleadoHook.empleados}
            setMostrarModalEmpleado={empleadoHook.setMostrarModalEmpleado}
            setEmpleadoSeleccionado={empleadoHook.setEmpleadoSeleccionado}
          />
        )}
        {pagina === "servicios" && (
          <PaginaServicios
            productos={servicioHook.servicios}
            agregarAlCarrito={() => {}}
            quitarProducto={servicioHook.eliminarServicio}
            setMostrarModalProducto={servicioHook.setMostrarServicioModal}
            setProductoSeleccionado={servicioHook.setServicioSeleccionado}
          />
        )}
        {pagina === "ventas" && <PaginaVentas />}
      </main>

      {/* Modales */}
      {clienteHook.mostrarModalCliente && (
        <ModalCliente
          cliente={clienteHook.clienteSeleccionado}
          cerrar={() => clienteHook.setMostrarModalCliente(false)}
          guardar={guardarCliente}
        />
      )}

      {empleadoHook.mostrarModalEmpleado && (
        <ModalEmpleado
          empleado={empleadoHook.empleadoSeleccionado}
          cerrar={() => empleadoHook.setMostrarModalEmpleado(false)}
          guardar={guardarEmpleado}
        />
      )}

      {servicioHook.mostrarModalServicio && (
        <ModalServicio
          servicio={servicioHook.servicioSeleccionado}
          cerrar={() => servicioHook.setMostrarServicioModal(false)}
          guardar={guardarServicio}
        />
      )}
    </div>
  );
}
